  text-align: center;
  margin: 0 1rem;
}

.page__content {
  padding: 1rem;
}



.form {
  flex-direction: column;
  width: 100%;
  display: flex;
}

.form__field__group {
  flex-direction: row;
  align-items: center;
  column-gap: 1rem;
  display: flex;
}

.form__field {
  margin: 0 0 1rem;
  display: block;
}

.form__field__label {
  margin: 0 0 .5rem;
  display: block;
}





.rhythm__viewer {
  border-radius: .25rem;
  margin: 1rem 0;
  display: block;
}

.rhythm__viewer .measure__viewer {
  width: 100%;
  max-width: 64rem;
  margin: 0 auto 1rem;
}



.measure__beats {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.measure__beats .beats {
  grid-gap: 1rem;
  flex: 1;
  grid-auto-rows: 1fr;
  justify-items: center;
  display: grid;
  overflow-x: auto;
}

.note__icon {
  margin: .5rem;
}

.note__icon.note__icon--current {
  transform: scale(1.5);
}

.beat {
  flex-direction: column;
  display: flex;
}

.beat .notes {
  flex-direction: column;
  flex: 1;
  padding: 1.5rem 0;
  display: flex;
}

.beat .notes .note {
  text-align: center;
  flex: 1;
}

.button--oper {
  margin: .5rem;
}







.rhythm__editor {
  padding-bottom: 6rem;
}

.rhythm__editor__footer {
  background-color: #fff;
  border-top: 1px solid #f0f0f0;
  align-items: center;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.form--player {
  flex: 1;
}

.rhythm__editor__footer .toolbar {
  flex-direction: row;
  width: 100%;
  max-width: 64rem;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
}

.rhythm__editor__footer .toolbar .toolbar__main {
  flex: 1;
}

.fill {
  flex: 1;
}

.audio__context__initializer .icon {
  margin: 2rem 0;
  font-size: 3rem;
}



.sight-ear .note__name {
  margin: 1rem 0;
  font-size: 5rem;
}

.sound__analyser__panel {
  text-align: center;
}

.sound__analyser__panel .note__name {
  margin: 1rem 0;
  font-size: 5rem;
}



.tuner .notes {
  margin: 2rem 0;
}

.tuner .instruments {
  text-align: center;
  margin: 1rem 0;
}

.tuner .input {
  text-align: center;
}

.tuner .input .note__name {
  margin: 1rem 0;
  font-size: 5rem;
